<template>
  <side-window>
    <div class="education social_media" ref="containerScroll">
      <div class="my_education sideScroll" ref="educ">
        <h1>
          EDUCATION
        </h1>
        <div class="my_certificate">
          <img src="../assets/images/course.png" class="certificate" alt="certificate" @click='openUrl'>
          <div class="certificate_text">
            <div class="about_course">
                <h2><a class="skillfactory_link" href="https://skillfactory.ru/python-for-web-developers" target="_blank">SKILLFACTORY</a></h2>
                <p>The 36 modules of this course gave me knowledge in: </p>
            </div>
            <div class="certificate_list">
            <ul>
              <li v-for="component in skillfactory_list" :key="component">
                {{component}}
              </li>
              <li>
                <a style="text-decoration: none;" href="https://skillfactory.ru/python-for-web-developers" target="_blank">Link to the course (in russian)</a>
              </li>
            </ul>
            </div>
          </div>
        </div>
        <p class="education_text">
            I'm also getting an associate's degree in computer engineering in June 2023<br>
        </p>
        <div class="scrollSlide fSS" ref="myEducation"></div>
      </div>
      <div class="my_social_media sideScroll" ref="socM">
          <div class="media_content">
              <h1>
                  SOCIAL MEDIA
              </h1>
              <div class="social">
                  <img src="@/assets/images/github.png" alt="" @click="githubPage">
                  <img src="@/assets/images/linkedin.png" alt="" @click="linkedinPage">
              </div>
              <p class="education_text social_text">
                  Here you can find more information about me and completed projects.
              </p>
          </div>
        <div class="scrollSlide sSS" ref="socialMedia"></div>
      </div>
      <div class="arrow_button">
        <button class="up_arrow arrow" ref="upBtn"
                @click="upScrollBtn"
                @mouseenter="scaleUpArr" @mouseleave="unScaleUpArr">
          <span class="up_arr btn_icon" ref="upArr"></span>
        </button>
        <button class="down_arrow arrow" ref="downBtn"
                @click="scrollDownBtn"
                @mouseenter="scaleDownArr" @mouseleave="unScaleDownArr">
         <span class="down_arr btn_icon" ref="downArr"></span>
        </button>
      </div>
    </div>
  </side-window>
</template>

<script>
import SideWindow from "@/components/UI/SideWindow";
export default {
  name: "SideMySocialMedia",
  components: {SideWindow},
  data() {
    return {
      skillfactory_list: ['• HTML, CSS and Bootstrap', '• JavaScript', '• Python', '• SQL', '• Django', '• Linux',
        '• Work with freelance orders',],
      activePanel: null,
      touchStartSide: 0,
    }
  },
  mounted() {
    this.scrollListenerSide();
    this.$refs.upBtn.style.background = 'rgba(22, 29, 40, 0.2)'
    this.$refs.upBtn.disabled;
  },
  methods: {
    githubPage() {
      window.open('https://github.com/Illmix', '_blank').focus();
    },
    linkedinPage() {
      window.open('https://www.linkedin.com/in/ilya-chaban-68971b238/', '_blank').focus();
    },
    scrollDownBtn() {
      this.$smoothScroll({
        container: this.$refs.containerScroll,
        scrollTo: this.$refs.socialMedia,
      })
      this.$refs.downBtn.style.background = 'rgba(2, 149, 219, 0.3)'
      this.$refs.upBtn.style.background = 'rgba(2, 149, 219, 0.8)'
      this.$refs.downBtn.disabled = true;
      this.$refs.upBtn.disabled = false;
    },
    upScrollBtn() {
      this.$smoothScroll({
        container: this.$refs.containerScroll,
        scrollTo: this.$refs.myEducation,
      })
      this.$refs.upBtn.style.background = 'rgba(22, 29, 40, 0.2)'
      this.$refs.downBtn.style.background = 'rgba(22, 29, 40, 0.8)'
      this.$refs.upBtn.disabled = true;
      this.$refs.downBtn.disabled = false;
    },
    scaleUpArr() {
      this.$refs.upArr.style.transform = 'scale(0.9)';
    },
    unScaleUpArr() {
      this.$refs.upArr.style.transform = 'scale(0.8)';
    },
    scaleDownArr() {
      this.$refs.downArr.style.transform = 'scale(0.9)';
    },
    unScaleDownArr() {
      this.$refs.downArr.style.transform = 'scale(0.8)';
    },
    scrollListenerSide() {
      this.$refs.socM.addEventListener('wheel', this.socialScroll)
      this.$refs.educ.addEventListener('wheel', this.educationScroll)
      this.$refs.socM.addEventListener('keydown', this.socialScroll)
      this.$refs.myEducation.addEventListener('keydown', this.educationScroll)
    },
    deleteListenerSide() {
      this.$refs.socM.removeEventListener('wheel', this.socialScroll)
      this.$refs.educ.removeEventListener('wheel', this.educationScroll)
      this.$refs.socM.removeEventListener('keydown', this.socialScroll)
      this.$refs.educ.removeEventListener('keydown', this.educationScroll)
    },
    educationScroll(e) {
      if (e.deltaY > 0) {
        this.deleteListenerSide();
        this.$smoothScroll({
          container: this.$refs.containerScroll,
          scrollTo: this.$refs.socialMedia,
        })
        this.$refs.downBtn.style.background = 'rgba(2, 149, 219, 0.3)'
        this.$refs.upBtn.style.background = 'rgba(2, 149, 219, 0.8)'
        this.$refs.downBtn.disabled = true;
        this.$refs.upBtn.disabled = false;
        setTimeout(()=>{this.scrollListenerSide()}, 500)
      }
    },
    socialScroll (e) {
      if (e.deltaY < 0) {
        this.deleteListenerSide();
        this.$smoothScroll({
          container: this.$refs.containerScroll,
          scrollTo: this.$refs.myEducation,
        })
        this.$refs.upBtn.style.background = 'rgba(22, 29, 40, 0.2)'
        this.$refs.downBtn.style.background = 'rgba(22, 29, 40, 0.8)'
        this.$refs.upBtn.disabled = true;
        this.$refs.downBtn.disabled = false;
        setTimeout(()=>{this.scrollListenerSide()}, 500)
      }
    },
    openUrl() {
      window.open('https://skillfactory.ru/python-fullstack-web-developer', '_blank');
    }

  }
}
</script>

<style scoped>
  .social_media {
    position: relative;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    width: 100%;
    height: 100%;
  }
  .sideScroll {
    position: relative;
  }
  .skillfactory_link {
    text-decoration: none;
    color: #161D28;
  }
  .scrollSlide {
    z-index: 999;
    position: absolute;
    min-height: 400px;
    min-width: 100px;
    top: 0;
    pointer-events: none;
    left: 50%;
  }

  .my_education {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 100svh;
    width: 100%;
  }

  .my_social_media {
      min-height: 100svh;
  }

  .my_education {
    background-color: #0295DB;
  }

  .my_social_media {
    background-color: #0B0E1B;
  }

  .my_education h1, .my_social_media h1 {
    font-size: clamp(2rem, 3vw, 5rem);
    margin-top: .5em;
    font-family: 'Roboto Slab', 'Arial', sans-serif;
  }

  .my_social_media h1 {
    color: #0295DB;
  }

  .my_education h1 {
    color: #161D28;
  }

  .my_certificate {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: min(6vh, 9rem);
    width: min(90%, 90rem);
  }

  .certificate_text {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #161D28;
    width: min(40%, 50rem)
  }

  .about_course {
    text-align: center;
  }

  .certificate_text h2 {
    font-size: clamp(1.2rem, 2vw, 5rem);
    font-family: 'Inter', sans-serif;
    color: #161D28;
  }

  .certificate_text h2 * {
    will-change: transform;
    transition: transform .5s, color .5s;
  }

  .certificate_text h2  *:hover {
    text-shadow: 2px 2px #161D28;
    transform: scale(1.05);
    color: white;
  }

  .certificate_text p {
    text-align: center;
    margin-top: .2em;
    font-size: clamp(1rem, 1.5vw, 2rem);
    margin-bottom: .3em;
    font-family: 'Roboto Slab', sans-serif, cursive;
    color: #161D28;
  }

  .certificate_text ul {
    font-size: clamp(1rem, 1.5vw, 2rem);
    font-family: 'Raleway', sans-serif;
    list-style-type: none;
    color: #161D28;
  }

  .certificate_text ul li {
    text-align: center;
    color: #161D28;
  }

  .certificate {
    width: min(50%, 70rem);
    will-change: transform;
    transition: transform .7s, box-shadow .7s;
    cursor: pointer;
  }

  .certificate:hover {
    transform: scale(1.03);
    box-shadow: 0 2rem 2rem rgba(0, 0, 0, 0.5);
  }

  @media only screen and (max-width: 28.125em) {
      .my_certificate {
          width: 95%;
          flex-direction: column;
      }
      .certificate_text {
          margin-top: min(5vh, 4rem);
          width: 100%;
      }
      .certificate {
          width: 100%
      }
  }

  .education_text {
    margin-top: min(5vh, 9rem);
    display: flex;
    justify-content: center;
    text-align: center;
    font-size: clamp(1.3rem, 1.7vw, 3rem);
    color: #161D28;
    font-family: 'Roboto Slab', sans-serif, cursive;
  }


  .media_content {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-around;
      height: 90%;
  }

  .social {
    display: flex;
    justify-content: space-around;
    width: min(90%, 90rem);
    max-height: 25%;
  }

  .social img {
    max-width: 100%;
    cursor: pointer;
    border-radius: 50%;
    will-change: transform;
    transition: transform .5s, box-shadow .5s;
  }

  @media only screen and (max-width: 46.875em) {
      .social {
          align-items: center;
          flex-direction: column;
      }
      .social img {
          margin-top: 10%;
          max-width: 50%;
      }
  }

  .social img:nth-child(1):hover {
    transform: scale(1.05);
    box-shadow: 0 0 50px #f1ab5d;
  }

  .social img:nth-child(2):hover {
    transform: scale(1.05);
    box-shadow: 0 0 50px #0295DB;
  }

  .arrow_button {
    position: fixed;
    transition: background .7s, right .7s;
    display: flex;
    flex-direction: column;
    right: -150%;
    bottom: clamp(.1rem, .1vh, 2rem);
    z-index: 3;
    text-align: center;
    margin: min(1vw, 1.5rem);
  }

  .arrow_button button {
    position: relative;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    display: block;
    will-change: transform;
    transition: transform .7s, background .7s;
    border-radius: 50%;
    width: clamp(2rem, 3vw, 5rem);
    height: clamp(2rem, 3vw, 5rem);
    outline: none;
    border: 0;
    cursor: pointer;
    background: rgba(22, 29, 40, 0.8);
    color: #fefefe;
    margin: clamp(.6rem, .5vh, 2rem) auto 0;
  }

  .up_arr {
    background-image: url("@/assets/images/up_arrow.png");
    background-position: 50%;
  }

  .down_arr {
    background-image: url("@/assets/images/down_arrow.png");
    background-position: 50%;
  }

  .btn_icon {
    display: block;
    height: 2.1rem;
    line-height: 2.4rem;
    background-size: 70%;
    background-repeat: no-repeat;
    will-change: transform;
    transition: transform .5s;
    transform: scale(0.8);
  }

  .social_text {
    color: #0295DB;
  }
</style>