<template>
  <div class="container fP">
    <video-background
        :src="require(`@/assets/bgvideo/video.mp4`)"
        :style="{height: '100svh', width: '100%', position: 'relative'}"
        :poster="require(`@/assets/images/backgrounds/first_video.png`)"
        overlay="rgba(0, 0, 0, 0.6)"
    >
        <div class="header_content">
          <h1 class="my_name" style="color: white;" ref="name" v-intersection="changeCurrentBlock">ILLIA_CHABAN</h1>
          <about-me-button v-scroll-to="'second_page'" ref="buttonAnim">
            ABOUT ME
          </about-me-button>
        </div>
    </video-background>
    <div class="observer"></div>
  </div>
</template>

<script>
import AboutMeButton from "@/components/UI/AboutMeButton";
import WebNavigation from "@/components/UI/WebNavigation";
export default {
  name: "WebHeader",
  components: {WebNavigation, AboutMeButton},
  data() {
    return {
      checker: true,
    }
  },
  mounted() {
    let string = this.$refs.name.innerHTML;
    this.$refs.name.innerHTML = '';
    function startAnim(x) {
      setTimeout(function() {document.getElementsByClassName('letter')[x].style.transition = 'opacity .5s';
        document.getElementsByClassName('letter')[x].style.opacity = '1';}, x*150)
    }
    for (let j = 0; j<string.length; j++) {
      this.$refs.name.innerHTML += '<span class="letter" style="opacity: 0;">'+string[j]+'</span>';
      startAnim(j)
    }
  },
  methods: {
    changeCurrentBlock() {
      if (this.checker)
      {
        this.$refs.buttonAnim.$el.style.opacity = '1';
        this.$refs.buttonAnim.$el.style.transform = 'scale(1)';
        this.checker = false
      }
    },
  },
}
</script>

<style scoped>
  @font-face {
    font-family: 'Special Elite';
    src: url('../assets/fonts/SpecialElite.ttf')
  }
  .header_content {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 100svh;
    padding-bottom: 6rem;
  }
  .my_name {
    font-size: clamp(37px, 7vw, 180px);
    font-family: 'Special Elite', 'Arial', sans-serif;
  }
</style>