<template>
  <div class="to_anim">
  <div class="button" @mouseover="onOverButton" @mouseleave="onLeaveButton">
    <div class="top_left_border top_borders border"></div>
    <div class="button_container">
      <div class="left_border side_borders border"></div>
      <a class="about">
        <slot>

        </slot>
      </a>
      <div class="right_border side_borders border"></div>
    </div>
    <div class="bottom_border border"></div>
  </div>
  </div>
</template>

<script>
export default {
  name: "AboutMeButton",
  methods: {
    onOverButton() {
      const bottom_border = document.getElementsByClassName('bottom_border')
      bottom_border[0].style.width = '100%';
      const top_borders = document.getElementsByClassName('top_borders')
      top_borders[0].style.width = '100%';
      const side_borders = document.getElementsByClassName('side_borders')
      side_borders[0].style.transitionDelay = '250ms';
      side_borders[1].style.transitionDelay = '250ms';
      side_borders[0].style.height = '100%';
      side_borders[1].style.height = '100%';
      const btn = document.getElementsByClassName('about')
      btn[0].style.transitionDelay = '0.5s';
      btn[0].style.backgroundSize = '100% 100%';
      btn[0].style.color = '#C996CC';
    },
    onLeaveButton() {
      const btn = document.getElementsByClassName('about')
      btn[0].style.transitionDelay = '0s';
      btn[0].style.backgroundSize = '0 100%';
      btn[0].style.color = 'darkred';
      const side_borders = document.getElementsByClassName('side_borders')
      side_borders[0].style.transitionDelay = '0.25s';
      side_borders[1].style.transitionDelay = '0.25s';
      side_borders[0].style.height = '0';
      side_borders[1].style.height = '0';
      const bottom_border = document.getElementsByClassName('bottom_border')
      const top_borders = document.getElementsByClassName('top_borders')
      setTimeout(() => { bottom_border[0].style.width = '35%'; }, 500);
      setTimeout(() => { top_borders[0].style.width = '35%'; }, 500);
    }
  }
}
</script>

<style scoped>

.to_anim {
  transition: opacity 750ms, transform 750ms;
  opacity: 0;
  transform: scale(0);
}

.button {
  margin-top: 1.3em;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: clamp(250px, 15.1vw, 370px);
  height: clamp(100px, 10.8vh, 150px);
  cursor: pointer;
  transition: transform 350ms;
}

.button:hover {
  transform: scale(1.15)
}

.about {
  font-size: clamp(30px, 1.78vw, 50px);
  color: darkred;
  font-family: 'Oswald', sans-serif;
  letter-spacing: 4px;
  padding: 3% 16%;
  background-image: url("@/assets/images/backgrounds/black.jpg");
  background-repeat: no-repeat;
  background-size: 0 100%;
  backface-visibility: hidden;
  transition: background-size 250ms, color 250ms;
}

.border {
  background-color: #C996CC;
  transition: width 250ms, height 250ms;
  width: 0;
  height: 3px;
}

.side_borders {
  width: 3px;
  height: 0;
  transition-delay: 500ms;
}

.button_container {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: flex-end;
}

.bottom_border,
.top_borders {
  width: 35%;
}
</style>
