<template>
  <side-window>
    <div class="projects">
      <div class="scrollSlide proj_observ1"></div>
      <h1 v-intersection="showProjects">PERSONAL PROJECTS</h1>
      <template v-if="show">
        <transition mode="out-in">
          <keep-alive>
            <Suspense>
                <projects/>
              <template #fallback>
                  <loader/>
              </template>
            </Suspense>
          </keep-alive>
        </transition>
      </template>
      <div class="scrollSlide proj_observ2"></div>
    </div>
  </side-window>
</template>

<script>
import SideWindow from "@/components/UI/SideWindow";
import Loader from "@/components/UI/Loader";
import { defineAsyncComponent } from 'vue'
const Projects = defineAsyncComponent(() =>
    import('@/components/Projects')
)
export default {
  name: "SideMyProjects",
  components: {Loader, SideWindow, Projects},
  data() {
    return {
      show: false,
    }
  },
  methods: {
    showProjects() {
      this.show = true;
    }
  },
}
</script>

<style scoped>

.projects {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #16213E;
  height: 100%;
  min-height: 100svh;
  overflow-y: auto;
  overflow-x: hidden;
  scrollbar-width: none;
}
.projects::-webkit-scrollbar {
  width: 0;
}
.projects h1 {
  text-align: center;
  font-size: clamp(2rem, 2.5vw, 3.5rem);
  font-family: 'Inter', 'Arial', sans-serif;
  color: #E94560;
}
.proj_observ1 {
    margin-top: 10px;
}
.proj_observ2 {
    margin-bottom: 10px;
}
</style>