<template>
  <div class="side__container">
    <div class="back_arrow" ref="back_arr" @click="surfBack" @mouseenter="onHover" @mouseleave="onUnHover">
      <svg
          width="70"
          height="40"
          viewBox="0 0 24 24"
          xmlns="http://www.w3.org/2000/svg"
      >
        <path ref="arrow"
            d="M20.3284 11.0001V13.0001L7.50011 13.0001L10.7426 16.2426L9.32842 17.6568L3.67157 12L9.32842 6.34314L10.7426 7.75735L7.49988 11.0001L20.3284 11.0001Z"
            :style="{fill: this.fillColor}"
        />
        <path ref="arrow2"
              d="M20.3284 11.0001V13.0001L7.50011 13.0001L10.7426 16.2426L9.32842 17.6568L3.67157 12L9.32842 6.34314L10.7426 7.75735L7.49988 11.0001L20.3284 11.0001Z"
              :style="{fill: this.fillColor}"
        />
      </svg>
    </div>
      <div class="side" ref="side">
        <slot>
        </slot>
      </div>
  </div>
</template>

<script>
export default {
  name: "SideWindow",
  data() {
    return{
      fillColor: 'black',
      counterAnim: true
    }
  },
  methods: {
    onHover() {
      this.fillColor = 'white';
      this.$refs.arrow.style.transition = 'transform .5s';
      this.$refs.arrow2.style.transition = 'transform .5s';
      if (this.counterAnim) {
        this.$refs.arrow2.style.opacity = '1';
        this.$refs.arrow.style.transform = 'translate3d(-100px, 0, 0)';
        this.$refs.arrow2.style.transform = 'translate3d(0, 0, 0)';
        this.counterAnim = false;
      }
      else {
        this.$refs.arrow.style.opacity = '1';
        this.$refs.arrow2.style.transform = 'translate3d(-100px, 0, 0)';
        this.$refs.arrow.style.transform = 'translate3d(0, 0, 0)';
        this.counterAnim = true;
      }
    },
    onUnHover() {
      this.fillColor = 'black';
      this.$refs.arrow.style.transition = '0s';
      this.$refs.arrow2.style.transition = '0s';
      if (!this.counterAnim) {
        this.$refs.arrow.style.opacity = '0';
        this.$refs.arrow.style.transform = 'translate3d(100px, 0, 0)';
        this.$refs.arrow.style.transitionDelay = '250ms';
      }
      else {
        this.$refs.arrow2.style.opacity = '0';
        this.$refs.arrow2.style.transform = 'translate3d(100px, 0, 0)';
        this.$refs.arrow2.style.transitionDelay = '250ms';
      }
    },
    surfBack() {
      this.$refs.back_arr.style.left = '-150%';
      this.$refs.side.style.left = '150%';
      document.querySelector('.arrow_button').style.right = '-150%';
    }
  }
}
</script>

<style scoped>
  .side__container {
    display: flex;
  }
  .side__container .side {
    transition: left .7s ease-in-out;
  }
  .side__container .back_arrow {
    transition: background-color .7s, left .7s, color .7s ease-in-out;
  }
  .back_arrow svg path:nth-child(1){
    will-change: transform;
    transition: transform .5s;
  }
  .back_arrow {
    display: flex;
    background-color: grey;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: -150%;
    height: 100%;
    width: 10%;
    cursor: pointer;
  }
  .back_arrow:hover {
    background-color: cornflowerblue;
  }
  .side {
    position: fixed;
    top: 0;
    left: 150%;
    height: 100%;
    width: 90%;
    z-index: 999;
  }
  .back_arrow svg path:nth-child(2) {
    will-change: transform;
    transition: transform .5s;
    transition-delay: 250ms;
    transform: translate3d(100px, 0, 0);
  }

  @media only screen and (max-width: 450px) {
    svg {
      width: 50px;
      height: 40px;
    }
    .back_arrow {
      width: 15%;
    }
    .side {
      width: 85%;
    }
  }
</style>