<template>
    <div class="project" @click="openUrl">
        <div class="project__info">
            <h3>{{pr.project.title}}</h3>
        </div>
        <div class="backImg" @mouseenter="showTech" @mouseleave="hideTech">
            <transition name="t">
                <div class="technologies" v-if="mouse_enter"><p v-html="pr.project.tech"></p></div>
            </transition>
            <img class="poster" :src=pr.project.post alt="">
        </div>
        <div class="project__info">
            <p>{{pr.project.body}}</p>
        </div>
    </div>
</template>

<script>
export default {
    name: "ProjectForm",
    props:{
        pr: {
            type: Object,
            required: true
        }
    },
    data() {
        return {
            mouse_enter: false,
        }
    },
    methods: {
        showTech() {
            this.mouse_enter = true;
        },
        hideTech() {
            this.mouse_enter = false;
        },
        openUrl() {
            window.open(this.pr.project.path, '_blank').focus();
        }
    }
}
</script>

<style scoped>

.project {
    color: #182533;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: clamp(25rem, 21vw, 45rem);
    height: 25rem;
    position: relative;
    box-shadow: 0 0 30px #533483;
    border-top: 0;
    background-color: #0F3460;
    cursor: pointer;
    transition: transform .3s;
    will-change: transform;
    border-radius: 10px;
    margin: .8rem;
}

.backImg {
    position: relative;
    overflow: hidden;
}

.poster {
    max-width: 100%;
    z-index: 1;
}

.technologies {
    background-color: rgba(0, 0, 0, 0.5);
    transform: translateY(0%);
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 2;
}
.t-enter-active, .t-leave-active {
    transition: 400ms transform ease;
}
.t-enter-from, .t-leave-to{
    transform: translateY(100%);
}
.technologies p {
    color: white;
    font-family: 'Inter', 'Arial', sans-serif, cursive;
    font-size: 1.2rem;
    padding-left: 5%;
    padding-top: 5%;
}
.project:hover {
    transform: scale(1.05);
}

.project__info {
    padding: 10px;
}

.project__info * {
    color: white;
    padding: .8em;
}
.project__info p {
    font-family: 'Raleway', 'Arial', sans-serif;
}
.project__info h3 {
    text-align: center;
    font-family: 'Roboto Slab', 'Arial', sans-serif;
    letter-spacing: 2px;
}

</style>