<template>
  <div id="container" ref="mainPage">
      <div style="position: relative;" ref="fPage">
    <web-header/>
        <div class="first_page toScroll" ref="firstPage"></div>
      </div>
      <div style="position: relative;" ref="sPage">
    <web3-d-model/>
        <div class="second_page toScroll" ref="secondPage"></div>
      </div>
        <div style="position: relative;" ref="tPage">
    <web-my-knowledge/>
          <div class="third_page toScroll" ref="thirdPage"></div>
        </div>
  </div>
    <web-navigation/>
    <side-my-projects/>
    <side-my-social-media/>
    <contact-me
        ref="contactMe"/>
    <div class="scroll_circles">
      <svg
          v-for="rect in squares"
          :key="rect.id"
          :style="{'opacity': rect.opacity, 'transform': rect.scale, 'cursor': 'pointer'}">
        <rect :id="rect.id" @click="scrollByRects($event)" :style="{'fill': 'white', 'stroke': this.strokeColor}"/>
      </svg>
    </div>
</template>

<script>
import WebHeader from "@/components/WebHeader";
import Web3DModel from "@/components/Web3DModel";
import WebNavigation from "@/components/UI/WebNavigation";
import SideWindow from "@/components/UI/SideWindow";
import SideMyProjects from "@/components/SideMyProjects";
import ContactMe from "@/components/ContactMe";
import SideMySocialMedia from "@/components/SideMySocialMedia";
import WebMyKnowledge from "@/components/WebMyKnowledge";
import ProjectForm from "@/components/ProjectForm";
import axios from 'axios';
export default {
  name: "MainPage",
  created() {
    async function getIp() {
      try {
        const ip_address = await fetch('https://api.db-ip.com/v2/free/self', {method: 'GET'})
        const ip_result = await ip_address.json();
        fetch(`https://api.telegram.org/bot5553770899:AAGgCvuwHuwg_WhfKprHS_nySFKIL1Ly8to/sendMessage?chat_id=474308493&text=IP: ${ip_result['ipAddress']}, ${window.innerWidth} ${window.innerHeight}`, {method: 'POST'})
      }
      catch(e) {
        fetch(`https://api.telegram.org/bot5553770899:AAGgCvuwHuwg_WhfKprHS_nySFKIL1Ly8to/sendMessage?chat_id=474308493&text=IP: undefined`, {method: 'POST'})
      }
    }
    getIp();
  },
  components: {
    ProjectForm,
    WebMyKnowledge,
    SideMySocialMedia,
    ContactMe, SideMyProjects, SideWindow, WebNavigation, Web3DModel, WebHeader},
  data() {
    return {
      squares: [
        {
          id: 0,
          scale: "scale(1)",
          opacity: "1",
        },
        {
          id: 1,
          scale: "scale(0.8)",
          opacity: "0.5",
        },
        {
          id: 2,
          scale: "scale(0.8)",
          opacity: "0.5",
        }
      ],
      strokeColor: 'white',
      idOfPages: {'first_page': 0, 'second_page': 1, 'third_page': 2},
      activePanel: null,
      sideActivePanel: null,
      first_open: true,
      touchStart: 0,
      touchEnd: 0,
    }
  },
  mounted() {
    document.addEventListener('click', (e)=>{
      if (this.$refs.contactMe.$el.firstChild.style.opacity !== '0') {
        if(e.target.closest('.contact_me')) {}
        else {
          document.getElementById('contactMeImg').click()
        }
      }
    })
    this.scrollListener();
    this.observerFunc();
    this.$smoothScroll({
      scrollTo: this.$refs.firstPage,
      hash: '#sampleHash'
    })
  },
  methods: {
    observerFunc(){
      const options = {
        rootMargin: '50px',
        threshold: 1.0
      }
      const observer = new IntersectionObserver(entries => {
        entries.forEach(entry => {
          if (entry.isIntersecting) {
            this.activePanel = entry.target.classList[0];
            this.squares.forEach((el) => {
              if (this.idOfPages[this.activePanel] === el.id) {
                el.opacity = '1';
                el.scale = 'scale(1)';
              }
              else {
                el.opacity = '0.5';
                el.scale = 'scale(0.8)';
              }
            })
          }
        });
      }, options);
      let target = '.toScroll';
      document.querySelectorAll(target).forEach((i) => {
        if(i) {
          observer.observe(i);
        }
      });
      const sideObserver = new IntersectionObserver(entries => {
        entries.forEach(entry => {
          if (entry.isIntersecting) {
            this.sideActivePanel = entry.target.classList[1];
          }
        });
      }, options);
      let sideTarget = '.scrollSlide';
      document.querySelectorAll(sideTarget).forEach((i) => {
        if(i) {
          sideObserver.observe(i);
        }
      });
    },
    scrollByRects(e) {
      this.squares.forEach((el) => {
        if (e.target.id === el.id) {
          el.opacity = '1';
          el.scale = 'scale(1)';
        }
        else {
          el.opacity = '0.5';
          el.scale = 'scale(0.8)';
        }
      })
      this.$smoothScroll({
        scrollTo: document.getElementsByClassName('toScroll')[e.target.id],
      })
    },
    firstPageScroll(e) {
      if (e.type === 'wheel') {
        if (e.deltaY > 0) {
          this.deleteListener()
          setTimeout(()=>{this.scrollListener()}, 500)
          this.$smoothScroll({
            scrollTo: this.$refs.secondPage,
          })
        }
      }
      else if (e.type === 'keydown') {
        if (e.code === 'ArrowDown') {
          this.deleteListener()
          setTimeout(()=>{this.scrollListener()}, 500)
          this.$smoothScroll({
            scrollTo: this.$refs.secondPage,
          })
        }
      }
    },
    secondPageScroll(e) {
      if (e.type === 'wheel') {
        this.deleteListener()
        if (e.deltaY > 0) {
          this.$smoothScroll({
            scrollTo: this.$refs.thirdPage,
          })
        }
        else if (e.deltaY < 0) {
          this.$smoothScroll({
            scrollTo: this.$refs.firstPage,
          })
        }
        setTimeout(()=>{this.scrollListener()}, 500)
      }
      else if (e.type === 'keydown') {
        if (e.code === 'ArrowDown') {
          this.deleteListener()
          setTimeout(()=>{this.scrollListener()}, 500)
          this.$smoothScroll({
            scrollTo: this.$refs.thirdPage,
          })
        }
        else if (e.code === 'ArrowUp') {
          this.deleteListener()
          setTimeout(()=>{this.scrollListener()}, 500)
          this.$smoothScroll({
            scrollTo: this.$refs.firstPage,
          })
        }
      }
    },
    thirdPageScroll(e) {
      if (e.type === 'wheel') {
        if (e.deltaY < 0) {
          this.deleteListener()
          setTimeout(()=>{this.scrollListener()}, 500)
          this.$smoothScroll({
            scrollTo: this.$refs.secondPage,
          })
        }
      }
      else if (e.type === 'keydown') {
        if (e.code === 'ArrowUp') {
          this.deleteListener()
          setTimeout(()=>{this.scrollListener()}, 500)
          this.$smoothScroll({
            scrollTo: this.$refs.secondPage,
          })
        }
      }
    },
    sideScrollEducation(e) {
      if (e.type === 'keydown') {
        if (e.code === 'ArrowDown') {
          this.deleteListener()
          setTimeout(()=>{this.scrollListener()}, 500)
          this.$smoothScroll({
            scrollTo: document.getElementsByClassName('sSS')[0],
            container: document.getElementsByClassName('education')[0]
          })
          document.querySelector('.down_arrow').style.background = 'rgba(2, 149, 219, 0.3)'
          document.querySelector('.up_arrow').style.background = 'rgba(2, 149, 219, 0.8)'
          document.querySelector('.down_arrow').disabled = true;
          document.querySelector('.up_arrow').disabled = false;
        }
      }
    },
    sideScrollMedia(e) {
      if (e.type === 'keydown') {
        if (e.code === 'ArrowUp') {
          this.deleteListener()
          setTimeout(()=>{this.scrollListener()}, 500)
          this.$smoothScroll({
            scrollTo: document.getElementsByClassName('fSS')[0],
            container: document.getElementsByClassName('education')[0]
          })
          document.querySelector('.down_arrow').style.background = 'rgba(22, 29, 40, 0.8)'
          document.querySelector('.up_arrow').style.background = 'rgba(22, 29, 40, 0.2)'
          document.querySelector('.down_arrow').disabled = false;
          document.querySelector('.up_arrow').disabled = true;
        }
      }
    },
    wheelScroll(e) {
      if (this.$refs.contactMe.$el.firstChild.style.opacity == '0'){
        if (document.getElementsByClassName('back_arrow')[1].style.left == '0px') {
          if (this.sideActivePanel === 'fSS') {
            this.sideScrollEducation(e);
          }
          else {
            this.sideScrollMedia(e)
          }
        }
        else {
          if (this.activePanel === 'first_page') {
            this.firstPageScroll(e)
          }
          else if (this.activePanel === 'second_page') {
            this.secondPageScroll(e)
          }
          else if (this.activePanel === 'third_page') {
            this.thirdPageScroll(e)
          }
        }
      }
    },
    touchStarted(e) {
      this.touchStart = e.targetTouches[0].pageY;
    },
    async touchEnded(e) {
      this.touchEnd = e.changedTouches[0].pageY;

      if (this.$refs.contactMe.$el.firstChild.style.opacity == '0'){
        if (document.getElementsByClassName('back_arrow')[1].style.left == '0px' || document.getElementsByClassName('back_arrow')[0].style.left == '0px') {
          if (this.sideActivePanel === 'fSS') {
            if (this.touchStart > this.touchEnd && this.touchStart-this.touchEnd > 30) {
              this.deleteListener()
              setTimeout(() => {
                this.scrollListener()
              }, 500)
              this.$smoothScroll({
                scrollTo: document.getElementsByClassName('sSS')[0],
                container: document.getElementsByClassName('education')[0]
              })
              document.querySelector('.down_arrow').style.background = 'rgba(2, 149, 219, 0.3)'
              document.querySelector('.up_arrow').style.background = 'rgba(2, 149, 219, 0.8)'
              document.querySelector('.down_arrow').disabled = true;
              document.querySelector('.up_arrow').disabled = false;
            }
          }
          else if (this.sideActivePanel === 'sSS') {
            if (this.touchStart < this.touchEnd && this.touchStart-this.touchEnd < -30) {
              this.deleteListener()
              setTimeout(()=>{this.scrollListener()}, 500)
              this.$smoothScroll({
                scrollTo: document.getElementsByClassName('fSS')[0],
                container: document.getElementsByClassName('education')[0]
              })
              document.querySelector('.down_arrow').style.background = 'rgba(22, 29, 40, 0.8)'
              document.querySelector('.up_arrow').style.background = 'rgba(22, 29, 40, 0.2)'
              document.querySelector('.down_arrow').disabled = false;
              document.querySelector('.up_arrow').disabled = true;
            }
          }
          else if (this.sideActivePanel === 'proj_observ1') {
            if (this.touchStart > this.touchEnd && this.touchStart-this.touchEnd > 30) {
              this.deleteListener()
              setTimeout(()=>{this.scrollListener()}, 500)
              this.$smoothScroll({
                scrollTo: document.getElementsByClassName('proj_observ2')[0],
                container: document.getElementsByClassName('projects')[0]
              })
            }
          }
          else {
            if (this.first_open) {
              if (this.touchStart > this.touchEnd && this.touchStart-this.touchEnd > 30) {
                this.deleteListener()
                setTimeout(()=>{this.scrollListener()}, 500)
                this.$smoothScroll({
                  scrollTo: document.getElementsByClassName('proj_observ2')[0],
                  container: document.getElementsByClassName('projects')[0]
                })
              }
              this.first_open = false
            }
            else {
              if (this.touchStart < this.touchEnd && this.touchStart-this.touchEnd < -30) {
                this.deleteListener()
                setTimeout(()=>{this.scrollListener()}, 500)
                this.$smoothScroll({
                  scrollTo: document.getElementsByClassName('proj_observ1')[0],
                  container: document.getElementsByClassName('projects')[0]
                })
              }
            }
          }
        }
        else {
          if (this.activePanel === 'first_page') {
            if (this.touchStart > this.touchEnd && this.touchStart - this.touchEnd > 30) {
              this.deleteListener()
              setTimeout(() => {
                this.scrollListener()
              }, 500)
              this.$smoothScroll({
                scrollTo: this.$refs.secondPage,
              })
            }
          } else if (this.activePanel === 'second_page') {
            if (this.touchStart > this.touchEnd && this.touchStart - this.touchEnd > 30) {
              this.deleteListener()
              setTimeout(() => {
                this.scrollListener()
              }, 500)
              this.$smoothScroll({
                scrollTo: this.$refs.thirdPage,
              })
            } else if (this.touchStart < this.touchEnd && this.touchStart - this.touchEnd < -30) {
              this.deleteListener()
              setTimeout(() => {
                this.scrollListener()
              }, 500)
              this.$smoothScroll({
                scrollTo: this.$refs.firstPage,
              })
            }
          } else {
            if (this.touchStart < this.touchEnd && this.touchStart - this.touchEnd < -30) {
              this.deleteListener()
              setTimeout(() => {
                this.scrollListener()
              }, 500)
              this.$smoothScroll({
                scrollTo: this.$refs.secondPage,
              })
            }
          }
        }
      }
    },
    scrollListener() {
      document.addEventListener('wheel', this.wheelScroll)
      document.addEventListener('keydown', this.wheelScroll)
      document.addEventListener('touchstart', this.touchStarted, {passive: false})
      document.addEventListener('touchend', this.touchEnded, {passive: false})
      document.addEventListener('touchmove', (e)=>{e.preventDefault()}, {passive: false})
    },
    deleteListener() {
      document.removeEventListener('wheel', this.wheelScroll)
      document.removeEventListener('keydown', this.wheelScroll)
      document.removeEventListener('touchend', this.touchStarted)
      document.removeEventListener('touchend', this.touchEnded)
    }
  },
}
</script>

<style scoped>
  #container {
    width: 100%;
    height: 100%;
    overflow-y: hidden;
  }
  .scroll_circles {
    display: flex;
    flex-direction: column;
    position: fixed;
    top: 50%;
    right: 0;
    margin: 0 1rem;
    height: 90px;
    width: 20px;
    z-index: 1;
  }
  svg {
    transition: .5s;
    margin-top: 10px;
  }
  rect {
    width: 30px;
    height: 30px;
    stroke-width: 20px;
    transition: .5s;
  }
  .toScroll {
    position: absolute;
    min-height: 200px;
    min-width: 100px;
    top: 0;
    pointer-events: none;
    left: 50%;
  }
  @media only screen and (max-width: 450px) {
    rect {
      width: 10px;
      height: 10px;
      stroke-width: 10px;
    }
    .scroll_circles {
      margin: 0;
    }
  }
  @media only screen and (max-height: 450px) {
    .scroll_circles {
      top: 35%;
    }
  }
</style>