<template>
  <div class="contact_me">
    <div class="contact_window" ref="contactWindow" :style="{opacity: this.opacityWindow, right: this.rightWindow}">
      <div class="chat" ref="chatPanel">
        <div></div>
      </div>
      <div class="scrollToMe"></div>
    </div>
    <div class="send_message_panel" ref="sendMessagePanel" :style="{opacity: this.opacityWindow, right: this.rightSendPanel}">
      <input type="text" id="message" ref="sendInput" placeholder="Write a message... " v-model="msg">
      <button class="btn send_message" @click="sendMessage" disabled ref="btn_send">Send</button>
    </div>
    <div id="contactMeBlock">
      <img id="contactMeImg" src="../assets/images/man.png" alt="" @click="showWindow">
      <p id="contactMeText">CONTACT ME!</p>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import {io, Socket} from "socket.io-client";

export default {
  name: "ContactMe",
  data() {
  return {
      socket: Socket,
      msg: '',
      messageListener: (message, side, createdAt) => {
          console.log(true)
          this.writeToScreen(message, side, createdAt)
          this.messages.push(message)
      },
      messages: [],
      scrollBarMinHeight: 0,
      opacityWindow: '0',
      counterClick: 1,
      rightWindow: '-100%',
      rightSendPanel: '-100%',
      scrollSendPanel: 87,
      messageAlreadySent: 0,
      nameChecked: false,
      name: '',
      botToken: '5553770899:AAGgCvuwHuwg_WhfKprHS_nySFKIL1Ly8to',
      chatId: 474308493,
      ip: 0,
      messageSent: false,
      firstMessage: true,
    }
  },
  async mounted() {
    document.addEventListener('keypress', (e)=>{
      if (e.code === 'Enter') {
        if (e.target === this.$refs.sendInput) {
          if (this.msg.trim().length !== 0) {
            this.sendMessage();
          }
        }
      }
    })
      // const ip_address = await fetch('https://api.db-ip.com/v2/free/self', {method: 'GET'})
      // const ip_result = await ip_address.json();
      // await axios.post('http://localhost:5000/bot/getUser', {content: ip_result['ipAddress']})
      //     .then(response => {
      //         if(!!response.data) {
      //             this.name = response.data.name;
      //             this.messages = response.data.messages
      //         }
      //     })
      // if (!!this.messages.length) {
      //     this.firstMessage = false;
      //     this.nameChecked = true;
      //     for (const message of this.messages) {
      //         const side = message['fromServer'] ? 'flex-start' : 'flex-end';
      //         this.writeToScreen(message.text, side, message['createdAt'])
      //     }
      // }
  },
  methods: {
    async botTelegram(ip, name, msg) {
      try {
        const requestOptions = {
        method: 'POST',
      };
        const sendMessage = await fetch(`https://api.telegram.org/bot${this.botToken}/sendMessage?chat_id=${this.chatId}&text=${ip}+:+${name}+:+${msg}`, requestOptions)
        const result = await sendMessage.json();
        if (result['ok']) {
            // const saveMessage = {text: `${ip}<field>${name}<field>${msg}`}
            // await axios.post('http://localhost:5000/bot', saveMessage)
          document.querySelector('.time_now').style.display = 'flex';
          document.querySelector('.time_now').className = '';
          if (this.firstMessage) {
            this.writeToScreen(`Thank for your message. I'll answer you as soon as I see the message.`, 'flex-start')
            document.querySelector('.time_now').className = '';
            this.firstMessage = false;
          }
        }
        else {
          document.querySelector('.time_now').innerHTML = 'Internal error. Try again later';
          document.querySelector('.time_now').style.color = 'red';
          document.querySelector('.time_now').style.display = 'flex';
          document.querySelector('.time_now').style.flexWrap = 'wrap';
          document.querySelector('.time_now').className = '';
        }
      }
      catch(e) {
        document.querySelector('.time_now').innerHTML = 'Internal error. Try again later';
        document.querySelector('.time_now').style.color = 'red';
        document.querySelector('.time_now').style.display = 'flex';
        document.querySelector('.time_now').style.flexWrap = 'wrap';
        document.querySelector('.time_now').className = '';
      }
    },
    async sendMessage() {
      const userMessage = this.msg;
      this.msg = '';
      let message_side = 'flex-end';
      this.writeToScreen(`${userMessage}`, message_side)
      if (this.nameChecked) {
        const ip_address = await fetch('https://api.db-ip.com/v2/free/self', {method: 'GET'})
        const ip_result = await ip_address.json();
        this.socket?.emit('message', 'side', 'createdAt')
        await this.botTelegram(ip_result['ipAddress'], this.name, userMessage);
      }
      else {
        let numberChecker = false;
        for (let c of userMessage) {
          if (isNaN(parseInt(c))) {numberChecker = true}
          else {
            numberChecker = false;
            break
          }
        }
        if (numberChecker) {
          if (userMessage.length > 15) {
            this.writeToScreen(`Oh. It doesn't look like your name. Please try again.`, 'flex-start')
          }
          else {
            this.writeToScreen(`Hi ${userMessage[0].toUpperCase()+userMessage.slice(1)}. Nice to meet you. What questions do you have?`, 'flex-start')
            this.nameChecked = true;
            this.name = userMessage[0].toUpperCase()+userMessage.slice(1);
          }
        }
        else {
          this.writeToScreen(`Oh. It doesn't look like your name. Please try again.`, 'flex-start')
        }
      }
      document.querySelector('.scrollToMe').scrollIntoView()
    },
    writeToScreen(message, side, messageTime=null) {
    let time_now
    if (!messageTime) {
        time_now = new Date().toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
    }
    else {
        time_now = new Date().toLocaleDateString("en-US") === new Date(messageTime).toLocaleDateString("en-US")
            ?
            new Date(messageTime).toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })
            :
            new Date(messageTime).toLocaleDateString("en-US", {month: '2-digit', day: '2-digit'}).replace('/', '.')
            +' '+new Date(messageTime).toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })
    }
    let pre = document.createElement("div");
    pre.style.display = 'flex';
    pre.style.justifyContent = side;
    let span = document.createElement('span');
    span.style.maxWidth = '75%';
    span.style.wordWrap = 'break-word';
    span.style.padding = '5px 10px';
    span.style.marginTop = '10px';
    span.style.color = 'white';
    span.style.borderRadius = '6px';
    span.style.backgroundColor = '#182533';
    span.style.fontFamily = 'Open Sans, sans-serif'
    span.innerHTML = message;
    let time = document.createElement('span');
    time.style.opacity = '0.5';
    if (this.nameChecked) {
      time.className = 'time_now';
      time.style.display = 'flex';
    }
    else {
      time.style.display = 'flex';
    }
    time.style.alignItems = 'flex-end';
    time.style.fontSize = '12px';
    time.style.color = 'white';
    time.innerHTML = time_now;
    if (side === 'flex-end') {
      time.style.marginRight = '5px';
      pre.appendChild(time);
      pre.appendChild(span);
    }
    else {
      time.style.marginLeft = '5px';
      pre.appendChild(span);
      pre.appendChild(time);
    }
    this.$refs.chatPanel.appendChild(pre);
   },

    showWindow() {
      if (this.counterClick) {
        this.rightWindow = '5%';
        this.rightSendPanel = '5%';
        this.opacityWindow = '1';
        this.counterClick = 0;
        if (!this.messageAlreadySent && !this.messages.length) {
          this.writeToScreen(`Hi! I'm glad to see you here. Could you tell me your name?
          <br>My email: <a href="mailto:illachaban1100@gmail.com" target="_blank" style="color: lightblue">illachaban1100@gmail.com</a><br>
          My resume: <a href="https://drive.google.com/file/d/1Wsxa3AF6mcyMH1pql7DSIClA7CVq4yWs/view?usp=sharing" target="_blank" style="color: lightblue"><br>Resume</a>`, 'flex-start')
          this.messageAlreadySent = 1;
        }
      }
      else {
        this.rightWindow = '-100%';
        this.rightSendPanel = '-100%';
        this.opacityWindow = '0';
        this.counterClick = 1;
      }
    }
  },
  watch: {
    msg() {
      this.$refs.btn_send.disabled = this.msg.trim().length === 0;
    },
    // socket: {
    //     handler: function () {
    //         this.socket = io("http://localhost:5001")
    //     },
    //       immediate: true
    // },
    // messageListener: {
    //     handler: async function () {
    //         this.socket?.on("message", this.messageListener)
    //         this.socket?.off("message", this.messageListener)
    //     },
    //       immediate: true
    // }
  }
}
</script>

<style scoped>
.contact_me {
  position: fixed;
  width: 30%;
  height: 30%;
  bottom: 0;
  right: 0;
  z-index: 2;
}
.contact_window, .send_message_panel {
  position: fixed;
  transition: right .5s, opacity .5s ease-out;
  width: 100%;
  min-width: 250px;
  border-radius: 15px;
  max-width: 400px;
  background-color: #0e1621;
}
.contact_window {
  bottom: 5%;
  padding: 10px;
  overflow-y: auto;
  overflow-x: hidden;
  height: 55%;
  min-height: 400px;
}

.contact_window::-webkit-scrollbar {
  display: none;
}

*:focus {
  outline: none;
}

.send_message_panel {
  height: 6%;
  bottom: 4%;
  padding: 6.5px 0;
  background-color: #0e1621;
  display: flex;
  justify-content: space-around;
  max-height: 55px;
}
.send_message_panel input {
  background-color: #182533;
  color: white;
  border: none;

}
.contact_window::-webkit-scrollbar {
  display: none;
}

#message {
  border-radius: 5px;
  font-family: 'Open Sans', sans-serif;
  padding: 10px;
  width: 55%;
}
.chat {
  margin-bottom: 20%;
}

.btn {
  border: none;
  outline: none;
  -webkit-tap-highlight-color: transparent;
  box-shadow: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px 15px;
  border-radius: 3px;
  font-size: 12px;
  text-transform: uppercase;
  width: 30%;
  color: white;
  font-family: 'Inter', sans-serif;
  background: #315efb;
  transition: box-shadow .3s, transform .3s;
  will-change: transform;
}

.btn:hover {
  box-shadow: 0 2px 8px 2px rgba(141,150,178,.3);
  transform: scale(1.05);
}

button:disabled,
button[disabled]{
  border: 1px solid #999999;
  background-color: #cccccc;
  color: #666666;
}

#contactMeImg {
  width: 100%;
  height: 100%;
  cursor: pointer;
  z-index: 1;
}

#contactMeBlock {
  position: absolute;
  width: 50px;
  height: 50px;
  bottom: 5%;
  right: 5%;
}
#contactMeText {
  width: 140%;
  z-index: 2;
  background-color: white;
  text-align: center;
  border-radius: 5px;
  font-size: 9px;
  letter-spacing: 0.5px;
  margin-left: -10px;
  margin-top: -2px;
  color: red;
  font-family: 'Arial', sans-serif;
}

@media only screen and (max-width: 450px) {
  .contact_window, .send_message_panel {
    max-width: 200px;
  }
}
@media only screen and (max-width: 275px) {
  .contact_window, .send_message_panel {
    min-width: 220px;
  }
}
@media only screen and (max-width: 650px) {
  .contact_window {
    bottom: 8%;
  }
  .send_message_panel {
    bottom: 8.3%;
  }
  #contactMeImg {
    width: 40px;
    height: 40px;
  }
  #contactMeText {
    margin-left: -17px;
  }
}

@media only screen and (max-height: 450px) {
  #contactMeImg {
    width: 35px;
    height: 35px;
  }
  #contactMeText {
    margin-left: -16px;
  }
}
</style>